@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{margin:0;font-family:"Roboto", sans-serif}.App{text-align:center}.App__form{position:relative;top:-4em;width:20%;border-radius:15px;box-shadow:6px 6px 19.5px grey}.App__top{background-color:#f6f6f6;height:8em;margin-bottom:8em;display:flex;justify-content:center;flex-direction:row}.App__top-logo{width:14em;height:5em;position:relative;top:1em;right:13px}.App__top-title{margin-top:0.5em;font-size:3em}.App__bottom{position:absolute;background-color:darkcyan;width:100%}.form{margin-top:10 em !important;display:flex;flex-direction:column}.form__input{width:21em;height:2em;font-size:x-large;text-align:justify;border-radius:6px}.form__btn{height:3em;width:8em;margin-left:62em;font-size:1em;margin:1em 75em;margin-bottom:6rem}.form__btn:hover{opacity:2;color:#787070;cursor:pointer}.form__label{margin-right:17em;margin-bottom:0.3em;font-size:19px}.member__list{display:flex;flex-direction:row;flex-wrap:wrap;justify-content:space-between;margin:0 56em}.member__title{font-size:3em}

